import React, { Fragment, useState, useEffect, useRef } from 'react';
import { Row, Col, Label } from 'reactstrap';
import ListPageHeading from 'containers/pages/ListPageHeading';
import Pagination from 'containers/pages/Pagination';
import { CSVLink } from 'react-csv';
import ImagePreView from 'components/PerviewImage/ModalView';
import { instructorsList } from 'Apis/admin';
import { NotificationManager } from 'components/common/react-notifications';
import { Link } from 'react-router-dom';
import ReactLoading from 'components/Loading';
import DeleteData from 'components/DeleteData';
import { convertDate } from 'constants/defaultValues';
import StatusUpdate from 'components/UpdateStatus';
import { additional, gender, ageRange } from './constants';
const Provider = React.memo((props) => {
	const CsvRef = useRef(null);
	const [pageInfo, setPageInfo] = useState(additional);
	const [totalPosts, setTotalPost] = useState([]);
	const [isLoading, setIsLoading] = useState(true);
	const [selectedPageSize, setSelectedPageSize] = useState(10);
	const [currentPage, setCurrentPage] = useState(1);
	const [searchText, setSearchtext] = useState(undefined);
	const [viewImage, setViewImage] = useState(false);
	const [imagePath, setImagePath] = useState('');
	const [filters, setFilters] = useState({
		gender: '',
		country: '',
		age: '',
	});
	const [csvData, setCsvData] = useState([]);
	const [loading, setLoading] = useState(false);

	useEffect(() => {
		instructorsList(
			currentPage,
			selectedPageSize,
			searchText,
			props.match?.params?.status || '',
			filters
		)
			.then((res) => {
				const { data } = res;
				const { result, pagination } = data.data;
				setIsLoading(false);
				setTotalPost(result);
				additional.totalItemCount = pagination.totalRecord;
				additional.selectedPageSize = pagination.limit;
				additional.totalPage = pagination.totalPage;
				setPageInfo({ ...additional });
			})
			.catch((err) => {
				setIsLoading(false);
				if (err.response) {
					const { data } = err.response;
					NotificationManager.warning(
						data.error_message,
						'Something went wrong',
						3000,
						null,
						null,
						''
					);
				}
			});
	}, [selectedPageSize, currentPage, searchText, filters]);
	const onSearchKey = (event) => {
		setSearchtext(event.target.value);
	};
	const changePageSize = (value) => {
		setIsLoading(true);
		setSelectedPageSize(value);
	};
	const onChangePage = (value) => {
		setCurrentPage(value);
	};
	const DeleteDataLocal = (key) => {
		totalPosts.splice(key, 1);
		setTotalPost([...totalPosts]);
	};
	const updateLocal = (value, key) => {
		totalPosts[key] = value;
		setTotalPost([...totalPosts]);
	};
	const handleLocalData = ({ target: { name, value } }) => {
		setFilters({ ...filters, [name]: value });
	};
	const handleClick = () => {
		setLoading(true);
		instructorsList(1, 1000, '', props.match?.params?.status || '', filters)
			.then((res) => {
				const { data } = res;
				const { result } = data.data;
				setLoading(false);
				const final = result.map((value) => {
					return {
						'Provider Name': value.name,
						'Provider Email': value.email,
						'Provider Phone': value.phone,
						'Provider gender': gender[value.gender],
						'Provider status': { 0: 'Inactive', 1: 'Active' }[value.status],
						'Profile Created': convertDate(value.created),
					};
				});
				setCsvData(final);
				CsvRef.current.link.click();
			})
			.catch((err) => {
				setLoading(false);
				if (err.response) {
					const { data } = err.response;
					NotificationManager.warning(
						data.error_message,
						'Something went wrong',
						3000,
						null,
						null,
						''
					);
				}
			});
	};
	const startIndex = (currentPage - 1) * selectedPageSize;
	const endIndex = currentPage * selectedPageSize;
	return isLoading ? (
		<div className='loading' />
	) : (
		<Fragment>
			<CSVLink
				data={csvData}
				filename={`instructor-record.csv`}
				style={{ display: 'none' }}
				ref={CsvRef}
			>
				Download
			</CSVLink>
			<ListPageHeading
				onClick={() => props.history.push('/add-provider')}
				addShow
				Addname='+ Add New Provider'
				match={props.match}
				heading='Provider'
				changePageSize={changePageSize}
				selectedPageSize={selectedPageSize}
				totalItemCount={pageInfo.totalItemCount}
				startIndex={startIndex}
				endIndex={endIndex}
				onSearchKey={onSearchKey}
				orderOptions={pageInfo.orderOptions}
				pageSizes={pageInfo.pageSizes}
			/>
			<ReactLoading loading={loading} />
			<Row className='mb-4'>
				<Col md='3'>
					<Label>Age Range</Label>
					<select
						onChange={handleLocalData}
						name='age'
						className='form-control'
					>
						<option value=''>--Select Age Range</option>
						{ageRange.map((value) => (
							<option key={value} value={value}>
								{value}
							</option>
						))}
					</select>
				</Col>
				<Col md='3'></Col>
				<Col md='3'></Col>
				<Col md='3'>
					<button className='btn btn-info ml-5 mt-4' onClick={handleClick}>
						<i className='simple-icon-arrow-down-circle mr-2'></i>Download CSV
					</button>
				</Col>
			</Row>
			<table className='table table-striped animate__animated  animate__zoomIn animate__fadeInDown'>
				<thead>
					<tr>
						<th>#</th>
						<th>Name</th>
						<th>Profile</th>
						<th>Email</th>
						<th>Phone</th>
						<th>Status</th>
						<th>Created Date</th>
						<th>Action</th>
					</tr>
				</thead>
				<tbody>
					{totalPosts.map((post, key) => (
						<tr key={key}>
							<td>{key + 1}</td>
							<td>
								<Link
									to={{
										pathname: '/provider-details',
										state: { post },
									}}
									className='d-flex'
								>
									{' '}
									{post.name}
								</Link>
							</td>
							<td>
								<img
									onClick={() => {
										setImagePath(post.profile);
										setViewImage(true);
									}}
									alt={post.first_name}
									src={post.profile}
									className='list-thumbnail responsive border-0 card-img-left'
								/>
							</td>
							<td>{post.email}</td>
							<td>{post.phone}</td>
							<td>
								<StatusUpdate
									data={post}
									table='users'
									statusMessage={{
										0: 'Inactive',
										1: 'Active',
									}}
									updateKey='status'
									onUpdate={() => updateLocal(key)}
									isButton
								/>
							</td>

							<td>{convertDate(post.created)}</td>
							<td>
								<Link
									to={{
										pathname: '/provider-details',
										state: { post },
									}}
									className='btn btn-primary btn-sm'
								>
									View
								</Link>{' '}
								<Link
									to={{
										pathname: '/edit-provider',
										state: { post },
									}}
									className='btn btn-info btn-sm'
								>
									Edit
								</Link>{' '}
								<DeleteData
									view='Provider'
									classes='btn-sm'
									table='users'
									data={post.id}
									ondelete={() => DeleteDataLocal(key)}
								>
									Delete
								</DeleteData>
							</td>
						</tr>
					))}
					{totalPosts.length === 0 && (
						<tr className='no-record-tr'>
							<td colSpan='10'>
								<h2 className='no-record'>No record Found</h2>
							</td>
						</tr>
					)}
				</tbody>
			</table>
			<ImagePreView
				imagePath={imagePath}
				showModel={viewImage}
				onClose={(value) => setViewImage(value)}
			/>
			<Pagination
				currentPage={currentPage}
				totalPage={pageInfo.totalPage}
				onChangePage={(i) => onChangePage(i)}
			/>
		</Fragment>
	);
});

export default Provider;
